@import url('https://fonts.googleapis.com/css2?family=Andada+Pro&family=Lora&family=Montserrat:ital@1&family=Nunito+Sans&family=Quintessential&family=Source+Code+Pro&family=Zen+Kurenaido&display=swap');
.outContainer{
    place-items: center;
    display: grid;
    height: 80vh;
    background-color: rgb(134, 68, 7);
    box-shadow: 0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
}
.inContainer{
    box-shadow: 0 12px 6px rgba(0,0,0,0.12),0 12px 13px rgba(0,0,0,0.24);
    width: 60%;
    padding: 10px;
    text-align:  center;
    background-color: rgb(235, 252, 1);
    border-radius:30px;
}

.inContainer>h1{
    font-family: 'Quintessential', cursive;
   

}
.home>Button{
    color:rgb(233, 250, 2);
    background-color: rgb(241, 157, 54);
}
